@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #FFF;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lists {
  margin-top: -150px;
}

footer {
  margin: 50px 0;
  text-align: center;
}

.loading {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99; 
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.movierow {
    margin-bottom: 30px;
    position: relative;
}

.movierow h2 {
    margin: 0px 0px 0px 30px;
}

.movierow--list {
    display: flex;
    transition: all ease 0.3s;
}

.movierow--listarea {
    overflow-x: hidden;
    padding-left: 20px;
}

.movierow--item {
    display: inline-block;
    width: 250px;
    cursor: pointer;
}

.movierow--item img {
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
}

.movierow--item img:hover {
    transform: scale(1);
}

.movierow--left, .movierow--rigth {
    position: absolute;
    width: 40px;
    height: 225px;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    top: 0;
    overflow: hidden;
    background-color: rgb(0, 0, 0, 0.6);
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}

.movierow--left {
    left: 0;

}

.movierow--rigth {
    right: 0;
}

.movierow:hover .movierow--left, 
.movierow:hover .movierow--rigth {
    opacity: 1;
}

@media (max-width: 760px) {
    .movierow--left,
    .movierow--rigth {
        opacity: 1;
    }
}
.featured {
  height: 100vh;
}

.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}

.featured--name {
  font-size: 45px;
  font-weight: bold;
}

.featured--info {
  font-size: 16px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points, .featured--year, .featured--seasons {
  display: inline-block;
  margin-right: 15px;
}

.featured--points {
  color: #46d369;
}

.featured--description {
  margin-top: 15px;
  font-size: 19px;
  color: #999;
  max-width: 40%;
}

.featured--buttons {
  margin-top: 15px;

}

.featured--watchbutton, .featured--mylistbutton {
display: inline-block;
font-size: 19px;
font-weight: bold;
padding: 11px 25px;
border-radius: 5px;
text-decoration: none;
margin-right: 10px;
opacity: 1;
transition: all ease 0.2s;
}

.featured--watchbutton:hover, .featured--mylistbutton:hover {
  opacity: 0.7;
}

.featured--watchbutton {
  background-color: #fff;
  color: #000;
}

.featured--mylistbutton {
  background-color: #333;
  color: #fff;
}

.featured--genres {
  margin-top: 15px;
  font-size: 16px;
  color: #999;
}

@media (max-width: 760px) {
  .featured {
    height: 90vh;
  }
  .featured--name {
    font-size: 40px;
  }
  .featured--info {
    font-size: 15px;
  }
  .featured--description {
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }
  .featured--watchbutton,
  .featured--mylistbutton {
    font-size: 16px;
  }
  .featured--genres {
    font-size: 14px;
  }
}
header {
    position: fixed;
    z-index: 999;
    top: 0;
    right: 0;
    left: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.5s;
}

header.black {
    background-color: #141414;
}

.header--logo {
    height: 25px;
}

.header--logo img {
   height: 100%; 
}

.header--user {
    height: 35px;
}

.header--user img{
    height: 100%;
    border-radius: 3px;
}
